:root {
  --textColor: #fdf6e3;
  --Base03: #002b36;
  --Base02: #073642;
  --Base01: #586e75;
  --Base00: #657b83;
  --Base0: #839496;
  --Base1: #93a1a1;
  --Base2: #eee8d5;
  --Base3: #fdf6e3;
  --accent: #c4dd8a;
}

body {
  background-image: url("blank-colors-desk-electronics.28a8d373.jpg");
  background-position: center;
  background-size: cover;
  font-family: Roboto, sans-serif;
}

h1 {
  text-align: center;
  margin-bottom: .75em;
  font-size: 3em;
  font-weight: 100;
}

a {
  color: var(--accent);
  padding: .2em .1em;
  transition: all .2s;
}

a:hover {
  background-color: var(--Base0);
}

a:visited {
  color: var(--accent);
}

.container {
  width: 580px;
  height: 100vh;
  color: var(--textColor);
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

#calculator {
  width: 580px;
  background-clip: padding-box;
  background-color: var(--Base01);
  border: 20px solid #07364233;
  padding: 20px;
}

#calculator-input {
  color: var(--textColor);
  background-color: var(--Base00);
  border-style: solid;
  border-width: 0 0 5px;
  border-color: var(--accent);
  text-align: right;
  width: 100%;
  outline: none;
  margin-bottom: .75em;
  padding: .5em;
  font-family: Roboto Mono, monospace;
  font-size: 2em;
}

#functions {
  width: 500px;
  justify-content: flex-end;
}

#digits {
  width: 375px;
}

#operators {
  width: 125px;
}

.keypad, .key-container {
  flex-wrap: wrap;
  display: flex;
}

.digit {
  width: 121px;
  height: 96px;
  background-color: var(--Base00);
  color: #fdf6e3;
  border: none;
  outline: none;
  margin: 2px;
  font-family: Roboto Mono, monospace;
  font-size: 1.5em;
  transition: all .2s;
}

.digit:hover {
  background-color: var(--Base0);
}

.digit:disabled {
  background-color: var(--Base01);
}

.footer {
  text-align: center;
  background-color: #07364233;
  padding: 1em;
}

